import React, { PureComponent } from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  message
} from 'antd'
import style from './style/Form.scss'
import { CsxSelect } from 'Form'
import { connect } from 'react-redux'
import { API_BUSINESSTYPEVALUES, API_WMSORDERTYPEKEYVALUES } from 'api'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'

@connect(
  state => ({ ...state.distributionRule }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }

  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    console.log(values)
    update({ searchParams: { ...searchParams, ...values } })
  }

  // 添加分配规则
  handleAdd() {
    const { handleAddOrEdit, searchParams, handleGetList } = this.props
    if (!searchParams.wmsOrderType) {
      message.error('请选择出库类型')
      return
    } else {
      handleAddOrEdit({
        data: {
          businessTypeCode: searchParams.businessType && searchParams.businessType.value
        }
      }, () => {
        message.success('添加成功')
        handleGetList()
      })
    }
  }

  // 重置
  reset = () => {
    const { update } = this.props
    update({ searchParams: {} })
  }

  render() {
    const {
      searchParams: {
        wmsOrderType,
        businessType,
      },
      searchParams,
      modalTuttonLoading
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <CsxSelect
              label="出库类型"
              require
              dateApi={API_WMSORDERTYPEKEYVALUES}
              selectItem={{ data: wmsOrderType, key: 'key', valueKey: 'value' }}
              onChangeSelect={(value) => {
                searchParams.businessType = {}
                this.onInputChange({ wmsOrderType: value })
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              label="业务类型"
              require
              dateApi={API_BUSINESSTYPEVALUES}
              searchParams={{ wmsOrderType: wmsOrderType && wmsOrderType.value }}
              selectItem={{ data: businessType, key: 'key', valueKey: 'value' }}
              errMessage='请先选择出库类型'
              onChangeSelect={(value) => {
                this.onInputChange({ businessType: value })
              }}
            />
          </Col>
          <Col span={12}>
            <Row type="flex" justify="end" >
              <Button type="primary" style={{ marginRight: 20 }} loading={modalTuttonLoading} onClick={() => { this.handleAdd() }}>添加</Button>
              <Button type="primary" onClick={() => { this.reset() }}>重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



