import React, {PureComponent} from 'react'
import {Table, Modal, message, Radio, Select} from 'antd'
const confirm = Modal.confirm
const RadioGroup = Radio.Group
const Option = Select.Option
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({
    ...state.distributionRule,
    userLoation: {...state.userLoation}
  }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {}
    this.columns = [
      {
        title: '操作',
        width: 100,
        dataIndex: '',
        key: '',
        render: (text, record, index) => (
          <div className='tableOperation'>
            <span
              className='blue'
              onClick={() => {
                this.onSave(text, record, index)
              }}
            >
              保存
            </span>
            <span
              className='red'
              onClick={() => {
                this.onDel(text, record, index)
              }}
            >
              删除
            </span>
          </div>
        )
      },
      {
        title: '出库类型',
        width: 100,
        dataIndex: 'wmsOrderType',
        key: 'wmsOrderType'
      },
      {
        title: '业务类型',
        width: 100,
        dataIndex: 'businessType',
        key: 'businessType'
      },
      {
        title: '支持超量发货',
        width: 100,
        dataIndex: 'fullAmount',
        key: 'fullAmount',
        render: (text, record, index) => {
          const businessTypeCodeString = record.businessTypeCode || ''
          /**
            {key: "24", value: "商超销售直送"}
            {key: "25", value: "商超销售直通"}
            {key: "55", value: "商超销售电商"}
            只有商超销售直送、商超销售直通、商超销售电商这三种业务类型可以选择 '不预占分配'类型
             *  */
          const isNoStand =
            (businessTypeCodeString === '24' ||
            businessTypeCodeString === '25' ||
            businessTypeCodeString === '55') ? true : false
          return (
            <RadioGroup
              value={record.overShippingType || 'N'}
              disabled={!isNoStand}
              onChange={e => {
                record.overShippingType = e.target.value
                console.log(record.overShippingType)
                this.forceUpdate()
              }}
            >
              <Radio value={'Y'}>是</Radio>
              <Radio value={'N'}>否</Radio>
            </RadioGroup>
          )
        }
      },
      {
        title: '分配规则',
        width: 220,
        dataIndex: 'distrabuteRuleDesc',
        key: 'distrabuteRuleDesc',
        render: (text, record, index) => {
          // 判断库存全量是否满足：满足可进行设置分配规则
          // 判断业务类型是否为 ‘寄售POS销售’ ， ‘自营POS销售’，如果是则分配规则都为不能选
          if (
            // record.fullAmountType == 'Y' ||
            record.businessType == '寄售POS销售' ||
            record.businessType == '自营POS销售'
          ) {
            return (
              <Select
                style={{width: 180}}
                defaultValue={record.distrabuteRule}
                onChange={this.handleChange}
                disabled={true}
              >
                <Option value={1}>按权重分配</Option>
                <Option value={2}>按门店优先级分配</Option>
                <Option value={3}>按创建时间优先分配</Option>
                <Option value={4}>按轮询分配</Option>
                <Option value={5}>按波次总量分配</Option>
              </Select>
            )
          } else {
            //非商超类型的业务不需要显示按波次总量分配的规则
            let businessTypeCodeString = record.businessTypeCode || ''
            if (
              businessTypeCodeString === '23' ||
              businessTypeCodeString === '24' ||
              businessTypeCodeString === '25' ||
              businessTypeCodeString === '55' ||
              businessTypeCodeString === '58' ||
              businessTypeCodeString === 'A23' ||
              businessTypeCodeString === 'A24' ||
              businessTypeCodeString === 'A25' ||
              businessTypeCodeString === 'A55' ||
              businessTypeCodeString === '06' ||
              businessTypeCodeString === '07'
            ) {
              return (
                <Select
                  style={{width: 180}}
                  defaultValue={record.distrabuteRule}
                  onChange={this.handleChange}
                >
                  <Option value={1}>按权重分配</Option>
                  <Option value={2}>按门店优先级分配</Option>
                  <Option value={3}>按创建时间优先分配</Option>
                  <Option value={4}>按轮询分配</Option>
                  <Option value={5}>按波次总量分配</Option>
                </Select>
              )
            } else {
              return (
                <Select
                  style={{width: 180}}
                  defaultValue={record.distrabuteRule}
                  onChange={this.handleChange}
                >
                  <Option value={1}>按权重分配</Option>
                  <Option value={2}>按门店优先级分配</Option>
                  <Option value={3}>按创建时间优先分配</Option>
                  <Option value={4}>按轮询分配</Option>
                </Select>
              )
            }
          }
        }
      }
    ]
  }

  handleChange = value => {
    const {update} = this.props
    update({distrabuteRule: value})
  }

  onDel(text, record, index) {
    const {handleGetList, handleDel} = this.props
    confirm({
      title: '温馨提示',
      content: '是否删除该条信息？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleDel({urlParams: [record.id]}, () => {
          message.success('删除成功')
          handleGetList()
        })
      }
    })
  }

  // 保存
  onSave(text, record, index) {
    const {handleGetList, handleSave, distrabuteRule} = this.props
    handleSave(
      {
        data: {
          id: record.id,
          overShippingType: record.overShippingType,
          distrabuteRule
        }
      },
      () => {
        message.success('保存成功')
        handleGetList()
      }
    )
  }

  // 判断是否有仓库，没有仓库就不显示列表数据
  componentDidMount() {
    const {handleGetList, userLoation} = this.props
    if (!userLoation || !userLoation.value) return
    handleGetList()
  }

  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList()
    }
  }

  render() {
    const {tableLoading, searchResult} = this.props
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 1050, y: 400}}
          onChange={this.onChange}
          pagination={false}
        />
      </div>
    )
  }
}



