import * as actionType from './actionType'
import {getData} from 'util'
import {API_DISTRIBUTIONRULES_PAGE, API_CREATEDISTRIBUTIONRULES_CREATE, API_DISTRIBUTIONRULES_DELETE, API_UPDATEDISTRIBUTIONRULES_SAVE} from 'api'
//同步
export const update = (object) => ({
  type: actionType.DISTRIBUTIONRULE_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.DISTRIBUTIONRULE_UPDATE, data: {tableLoading: true}})
  getData({api: API_DISTRIBUTIONRULES_PAGE, ...reqData}).then(data => {
    console.log(data)
    // 相应的逻辑判断
    dispatch({
      type: actionType.DISTRIBUTIONRULE_UPDATE,
      data: {
        tableLoading: false,
        searchResult: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.DISTRIBUTIONRULE_FILE
    })
  })
}

// 添加分配规则
export const handleAddOrEdit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.DISTRIBUTIONRULE_UPDATE, data: {modalTuttonLoading: true}})
  let api = API_CREATEDISTRIBUTIONRULES_CREATE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.DISTRIBUTIONRULE_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.DISTRIBUTIONRULE_FILE
    })
  })
}

// 删除
export const handleDel = (reqData, callBack) => (dispatch) => {
  getData({api: API_DISTRIBUTIONRULES_DELETE, ...reqData}).then(data => {
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.DISTRIBUTIONRULE_FILE
    })
  })
}


// 保存
export const handleSave = (reqData, callBack) => (dispatch) => {
  getData({api: API_UPDATEDISTRIBUTIONRULES_SAVE, ...reqData}).then(data => {
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.DISTRIBUTIONRULE_FILE
    })
  })
}



